import angular from 'angular';

import memoryUtilService from './memory-util-service.provider';
import utilService from './util-service.provider';

const ngModule = angular.module('fis.core.utils', []);

ngModule.provider('fisMemoryUtilService', memoryUtilService);
ngModule.provider('fisUtilService', utilService);

export default ngModule.name;



