export default function ($compile) {
  'ngInject';

  return {
    restrict: 'A',
    priority: 590,
    terminal: true,
    compile: function () {
      return function postLink(scope, element, attrs) {
        element.addClass(scope.$eval(attrs.fisApplyClassDirective));

        $compile(element, null, 590)(scope);
      };
    }
  };
}



