/**
 * @class
 * @name fisThemeService
 * @description fisConfig service, retrieves configuration for shell
 */
export default function () {
  var self = this;

  self.$get = [function () {
    var serv = {};

    var userInContext;

    serv.context = function (user) {
      if (arguments.length === 1) {
        userInContext = user;
      } else {
        var clonedUser = angular.copy(userInContext);
        return clonedUser;
      }
    };
    return serv;
  }];
}



