import angular from 'angular';

import hookDirective from './hook.directive';
import hookListDirective from './hook-list.directive';

const ngModule = angular.module('fis.core.hook', []);

ngModule.directive('fisHook', hookDirective);
ngModule.directive('fisHookList', hookListDirective);

export default ngModule.name;



