import angular from 'angular';

import draggable from './draggable.directive';
import dropTarget from './drop-target.directive';

const ngModule = angular.module('fis.core.directives.dragdrop', []);

ngModule.directive('fisDraggable', draggable);
ngModule.directive('fisDropTarget', dropTarget);

export default ngModule.name;



