import angular from 'angular';

import pubsub from './pubsub.directive';
import pubsubService from './pubsub.service';

const ngModule = angular.module('fis.core.pubsub', []);

ngModule.directive('fisPubSub', pubsub);
ngModule.service('fisPubSubService', pubsubService);

export default ngModule.name;



