import angular from 'angular';

import interpolate from './interpolate.filter';
import search from './search.filter';
import uncamelcase from './uncamelcase.filter';

const ngModule = angular.module('fis.core.filters', []);

ngModule.filter('interpolate', interpolate);
ngModule.filter('search', search);
ngModule.filter('uncamelcase', uncamelcase);

export default ngModule.name;



