    var basicConstraints = [
        { value: 'eq', label: 'is' },
        { value: 'ne', label: 'is not' },
        { value: 'is_null', label: 'is null' },
        { value: 'not_null', label: 'is not null' }
    ];

    var numberConstraints = basicConstraints.concat([
        { value: 'gt', label: 'greater than' },
        { value: 'ge', label: 'greater than or equal to' },
        { value: 'lt', label: 'less than' },
        { value: 'le', label: 'less than or equal to' }
    ]);

    var defaultConstraints = basicConstraints.concat([
        { value: 'contains', label: 'contains' },
        { value: 'not_contains', label: 'not contains' },
        { value: 'is_empty', label: 'is empty' },
        { value: 'not_empty', label: 'is not empty' },
        { value: 'startswith', label: 'starts with' },
        { value: 'not_startswith', label: 'not starts with' },
        { value: 'endswith', label: 'ends with' },
        { value: 'not_endswith', label: 'not ends with' }
    ]);

    export default {
      string: defaultConstraints,
      integer: numberConstraints,
      currency: numberConstraints,
      percentage: numberConstraints,
      number: numberConstraints,
      double: numberConstraints,
      date: numberConstraints,
      datetime: numberConstraints,
      time: numberConstraints,
      defaultType: defaultConstraints,
      boolean: basicConstraints
    };



